 :root{
    --main-side-color:#263238;
     --main-bg-color:#ffe0b2;

} 

body {
     background-color: #ffe0b2;
     background-color: var(--main-bg-color);
     background-image: linear-gradient(to right, rgb(198, 255, 221), rgb(251, 215, 134), rgb(247, 121, 125));
}

.App {
     display: flex;
}
.content {
	background-color: rgba(255, 255, 255, 0.199);
}
.main-content {
	margin-top: 30px;
}
.sidebar {
	background: var(--main-side-color);
	width: 20%;
	min-width: 200px;
	height: 100vh;
	display: flex;
	flex-direction: column;
	position: -webkit-sticky;
	position: sticky;
	top: 0px;
	padding-top: 30px;
}
.sidebar-item {
	display: flex;
	flex-direction: column;
}
.nav-item {
	padding: 10px 40px;
	color: white;
}
.nav-item:hover {
	color: white;
	background-color: rgb(54, 54, 54);
	text-decoration: none;
}
.active {
	color: var(--main-bg-color);
}
.profile-img {
	text-align: center;
}
.profile-img > img {
	width: 70%;
	border-radius: 50%;
}
.profile-about {
	color: white;
	text-align: center;
}
.icon {
	color: white;
	margin: 10px 5px;
}
hr {
	background-color: white;
	width: 70%;
	border-radius: 30%;
}
.img {
	border-radius: 5%;
	max-height: 300px;
}
.text {
	align-items: center;
	display: flex;
}
h1 {
	font-size: 30px;
	font-family: 'Sriracha', cursive;
}
.h2, h3, h4 {
	font-family: 'Sriracha', cursive;
}
.bottom {
	margin-left: 2%;
}
h2 {
	color: transparent;
}
Button {
	margin-right: 16px;
	background-color: yellow;
}
p {
	font-family: 'Montserrat', sans-serif;
}
.profile-about > p {
	font-size: 15px;
}
.my-timeline h3,.my-timeline h2, .my-timeline h4, .my-timeline p {
	font-family: 'Montserrat', sans-serif;
	font-size: medium;
}
.my-timeline h3 {
	font-weight: 600;
}
